.projects-grid {
  gap: 2.5rem;
}

/* Empty State */
.projects-empty-state {
  text-align: center;
  padding: 4rem 2rem;
  color: rgba(255,255,255,0.6);
  font-size: 1.1rem;
}

/* Card Container */
.glass-card {
  position: relative;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(18px);
  -webkit-backdrop-filter: blur(18px);
  border-radius: 18px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  overflow: hidden;
  box-shadow: 0 20px 45px rgba(0, 0, 0, 0.35);
  transition: all 0.35s ease;
}

.glass-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 30px 80px rgba(74, 144, 226, 0.35);
  border-color: rgba(74, 144, 226, 0.4);
}

/* IMAGE SECTION */

.project-image-wrapper {
  position: relative;
  width: 100%;
  height: 230px;
  overflow: hidden;
}

.project-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.6s ease;
}

.glass-card:hover .project-image-wrapper img {
  transform: scale(1.08);
}

/* Dark overlay for readability */
.image-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.15),
    rgba(26, 26, 46, 0.9)
  );
  pointer-events: none;
}



/* CONTENT SECTION*/

.project-content {
  padding: 1.8rem;
}

/* Title */
.project-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 0.6rem;
}

/* Description */
