.admin-section textarea:focus,
.admin-section select:focus {
  outline: none;
  border-color: #f0a500; /* golden accent on focus */
  box-shadow: 0 0 6px rgba(240, 165, 0, 0.5);
}

/* File input */
.admin-section input[type="file"] {
  background-color: #1f1f38;
  color: #f0f0f0;
  padding: 6px;
}

/* Buttons inside forms */
.admin-section button {
  background-color: #3498db;
  color: #fff;
  border-radius: 6px;
  padding: 8px 16px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.admin-section button:hover {
  background-color: #2980b9;
}
.admin-section > div {
  background-color: #1f1f38; /* dark card */
  color: #eee; /* text inside card */
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 12px;
}

.admin-section h3 {
  color: #f0a500; /* accent headings */
}

.admin-section p {
  color: #ddd; /* readable text */
}
/* ===== LIST ITEMS / SKILLS ===== */
.admin-section ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 10px 12px;
  border-radius: 6px;
  background-color: #2c2c44; /* dark background for items */
  color: #eee; /* light text for readability */
  border: 1px solid #444; /* subtle border for separation */
  word-break: break-word;
}

/* Buttons inside list items */
.admin-section ul li button {
  padding: 4px 10px;
  font-size: 0.85rem;
  border-radius: 4px;
  background-color: #e74c3c; /* delete button */
  color: #fff;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

.admin-section ul li button:hover {
  background-color: #c0392b;
}

/* Links inside list items */
.admin-section ul li a {
  color: #3498db; /* optional links */
  text-decoration: underline;
}

.admin-section ul li a:hover {
  color: #2980b9;
}
/* Featured checkbox alignment and color */
.admin-section form label {
  display: flex;
  align-items: center;     
  gap: 8px;                 
  color: #c8b3b3;          
  margin: 10px 0 12px 0;
  cursor: pointer;
  font-size: 0.95rem;
  line-height: 1.2;
}

/* Checkbox sizing */
.admin-section form label input[type="checkbox"] {
  margin: 0;
  padding: 0;
  vertical-align: middle;
  width: 16px;
  height: 16px;
}

/* MOBILE RESPONSIVE STYLES Applies to screens ≤768px */
@media (max-width: 768px) {
  /* NAVBAR*/
  .nav-toggle {
    display: block;
    cursor: pointer;
  }

  .nav-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: rgba(10, 14, 39, 0.95);
    backdrop-filter: blur(20px);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding: 1rem 2rem;
    z-index: 999;
  }

  .nav-menu.active {
    display: flex;
  }

  .nav-link {
    padding: 1rem 0;
    font-size: 1.1rem;
  }

  .nav-container {
    padding: 1rem 1.5rem;
  }

  /* HERO SECTION*/
  .hero-container {
    margin-left: 1rem;
    max-width: 90%;
    padding: 1rem;
  }

  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-content h2 {
    font-size: 1.2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }

  .social-links {
    justify-content: flex-start;
  }

  .profile-photo {
    width: 200px;
    height: 200px;
  }

  /*  SKILLS PAGE*/
  .skills-grid {
    grid-template-columns: 1fr !important;
    gap: 1rem;
    padding: 1rem 0;
  }

  .skill-card {
    padding: 1.5rem 1rem;
  }

  .skills-page h1 {
    font-size: 2.2rem;
  }

  .skills-page .header-icon {
    font-size: 2rem;
  }

  /* EXPERIENCE PAGE*/
  .experience-timeline::before {
    left: 2rem !important;
  }

  .experience-card {
    width: 100% !important;
    left: 0 !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }

  .experience-card::before {
    left: 1.25rem !important;
  }

  .experience-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .experience-page h1 {
    font-size: 2.4rem;
  }

  /* PROJECTS PAGE */
  .projects-header-glass {
    padding: 2rem 1.5rem;
  }

  .projects-header-glass h1 {
    font-size: 2.4rem;
  }

  .resume-download-btn {
    width: 100%;
    justify-content: center;
  }

  .projects-grid {
    grid-template-columns: 1fr !important;
    gap: 1.5rem;
  }

  /* Project card adjustments */
  .project-image-wrapper {
    height: 200px;
  }

  .project-content {
    padding: 1.4rem;
  }

  .project-title {
    font-size: 1.25rem;
  }

  /* Tech tags */
  .project-tech {
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  /* RESUME / SKILLS ADMIN PAGE */
  .resume-container,
  .skills-container {
    grid-template-columns: 1fr !important;
    gap: 1rem;
  }

  .skills-list {
    max-height: 300px;
  }

  /* Admin buttons smaller */
  .btn-sm {
    padding: 0.25rem 0.5rem;
    font-size: 0.8rem;
  }

  /* CONTACT PAGE*/
  .contact-content {
    grid-template-columns: 1fr !important;
    gap: 2rem;
  }

  .contact-item {
    flex-direction: column;
    align-items: flex-start;
  }

  /* GENERAL ADJUSTMENTS */
  .container {
    padding: 0 1rem;
  }

  /* ADMIN (mobile: long URLs / tighter spacing) */
  .admin-section a {
    display: block;
    max-width: 100%;
    word-break: break-all;       
    overflow-wrap: anywhere;     
    white-space: normal;        
    line-height: 1.4;
  }

  .admin-section > div {
    padding: 14px;
  }

  .admin-section p {
    font-size: 0.95rem;
    line-height: 1.5;
  }

  /* GLOBAL / HOME (mobile scroll + layout) */
  html,
  body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    height: auto;
    -webkit-overflow-scrolling: touch;
  }

  .home-page,
  .container,
  .main-content {
    width: 100%;
    max-width: 100%;
    padding: 16px;
    box-sizing: border-box;
  }

  section,
  .section {
    min-height: auto;
    height: auto;
  }

  h1 {
    font-size: 1.8rem;
    line-height: 1.2;
  }

  h2 {
    font-size: 1.4rem;
  }

  h3 {
    font-size: 1.1rem;
  }

  p {
    font-size: 0.95rem;
    line-height: 1.6;
  }

  img {
    max-width: 100%;
    height: auto;
    display: block;
  }

  .card,
  .project-card,
  .experience-card,
  .cert-card {
    width: 100%;
    margin-bottom: 16px;
  }

  .flex,
  .row,
  .grid {
    flex-direction: column !important;
  }

  /* Home/hero height + spacing */
  .home {
    height: auto !important;
    overflow: visible !important;
    min-height: 100vh;
  }

  .hero {
    height: auto !important;
    min-height: 100vh;
    padding-top: 120px;
    padding-bottom: 60px;
  }

  /* Hero fine-tuning */
  .hero-container {
    margin-left: 0;
    max-width: 100%;
    padding: 1.5rem;
  }

  .hero-content h1 {
    font-size: 2.2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .hero-buttons {
    flex-direction: column;
  }

  /* Prevent image cropping on mobile hero/profile */
  .hero-image img {
    height: auto !important;
    max-height: none !important;
    width: 100%;
    object-fit: contain;
    background: #1a1a2e;
  }

  .profile-photo {
    width: 220px;
    height: auto;          
    max-height: 300px;
    object-fit: contain;  
    border-radius: 16px;   
    transform: none !important;
    opacity: 1;
  }

  /* EXPERIENCE PAGE (mobile timeline polish) */
  .experience-timeline {
    max-width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    margin: 0 auto;
  }

  .experience-timeline::before {
    left: 1.5rem !important;
  }

  .experience-card {
    width: 100% !important;
    left: 0 !important;
    padding-left: 3rem !important;
    padding-right: 1rem !important;
    margin-bottom: 1.5rem;
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(8px);
    border-radius: 12px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  }

  .experience-card::before {
    left: 0.5rem !important;
    right: auto !important;
    top: 1.5rem;
    width: 14px;
    height: 14px;
    border-width: 3px;
  }

  .experience-header {
    flex-direction: column !important;
    align-items: flex-start !important;
    gap: 0.5rem;
  }

  .experience-title h3 {
    font-size: 1.2rem !important;
  }

  .company-info h4 {
    font-size: 1rem !important;
  }

  .experience-duration {
    font-size: 0.85rem;
    padding: 0.4rem 0.8rem;
  }

  .experience-description {
    font-size: 0.95rem;
    line-height: 1.5;
  }

  .experience-skills {
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .skill-tag {
    font-size: 0.8rem;
    padding: 0.25rem 0.6rem;
  }

  .experience-card:nth-child(odd),
  .experience-card:nth-child(even) {
    left: 0 !important;
    padding-left: 3rem !important;
    padding-right: 1rem !important;
  }

  .experience-item-admin {
    flex-direction: column;
    gap: 0.5rem;
  }

  .admin-section form {
    flex-direction: column;
    gap: 1rem;
  }

  .date-inputs {
    grid-template-columns: 1fr !important;
  }
}
.hero-image img,
.profile-image img,
.about-image img {
  object-fit: cover;
  object-position: top center;
}